#contact p {
    margin: 20px 0;
}
#contact input, #contact textarea {
    border: 2px solid #666;
    font-family: 'VG';
}
#contact button.ui.button.blue {
    font-family: 'VG';
    width: 100%;
    margin-bottom: 10px;
}
#contact .ui.positive.message {
    background-color: #21BA45;
}
#contact .ui.negative.message {
    background-color: #DB2828;
}
#contact .ui.message .header{
    color: white;
    font-family: 'VG';
}
div#sending-mail {
    height: 100px;
    text-align: center;
    font-size: 40px;
    padding: 20px;
    margin: 40px;
}
@media screen and (max-width: 768px){
    #contact input, #contact textarea {
        font-size: 12px;
    }
}